$cellSize: 32px;

.Cell {
  display: inline-block;
  width: $cellSize;
  height: $cellSize;
  float: left;
  border-top: var(--cellBorder);
  border-left: var(--cellBorder);
  background-color: darkgrey;
  font: inherit;
  font-size: 18px;
  font-weight: bold;
  line-height: $cellSize;
  text-align: center;

  &__visible {
    background-color: lightgrey;
  }

  &__busted {
    background-color: red;
  }

  &__wrong {
    position: relative;

    &::before,
    &::after {
      content: '';
      width: 2px;
      height: 100%;
      background: red;
      position: absolute;
      top: 0;
      left: 45%;
    }

    &::before {
      transform: rotate(-45deg);
    }

    &::after {
      transform: rotate(45deg);
    }
  }

  &__1 {
    color: blue;
  }

  &__2 {
    color: green;
  }

  &__3 {
    color: red;
  }

  &__4 {
    color: purple;
  }

  &__5 {
    color: maroon;
  }

  &__6 {
    color: turquoise;
  }

  &__7 {
    color: black;
  }

  &__8 {
    color: darkgrey;
  }
}
